import React from 'react'
import "./ProductPage.css";
import {useStateValue} from './StateProvider'

function ProductPage({ id, title, image, price, rating, description }) {
    const [ {basket}] = useStateValue();
    // const [{ basket}, dispatch] = useStateValue();
    
/*     const addToBasket = () => {
        // Add item to basket
        dispatch({
            type: 'ADD_TO_BASKET',
            item: {
                id: id,
                title: title,
                image: image,
                price: price,
                rating: rating,
            }
        })
    }; */
    return (
        <div className="productPage">
            <div className="productPage__left">
            <div className="productPage__info">
                <p>{basket[basket.length-1].title}</p>
                <div className="productPage__rating">
                    { 
                        Array({rating})
                        .fill()
                        .map((_) => {
                            <p>:star:</p>
                        }) 
                    } 
                </div>
            </div>
            
			<img className="productPage__Image" src={basket[basket.length-1].image} alt=""/>
			{/* <button className="productPage__Button" onClick={addToBasket}>Add to basket</button> */}
            <p>{basket[basket.length-1].description}</p>
            </div>
            <div className="productPage__right">
                <p className="productPage__price"> 
                    <small>$</small>
                    <strong>{basket[basket.length-1].price}</strong>
                </p>
                <button>Proceed to Checkout</button>
            </div>
        </div>
    )
}

export default ProductPage
